import React, { useContext, useState } from 'react';
import {Link} from 'react-router-dom';
import {Button} from '@mui/material';
import {CameraAltOutlined} from '@mui/icons-material';

import '../auth/Auth.css';
import './AddPost.css';
import { userContext } from '../../providers/userProvider';
import { notificationContext } from '../../providers/notificationProvider';
import { addPost } from '../../http/user';

const AddPost = () => {
    const {user} = useContext(userContext);
    const [file,setFile] = useState();
    const [imgPrev, setImgPrev] = useState();
    const [content, setContent] = useState();

	const {setMessage,setError} = useContext(notificationContext);

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			const formData = new FormData();
            formData.append('file',file);
            formData.append('content',content);
			const {data: resData} = await addPost(formData);
			setMessage(resData.message);
            setContent('');
            setImgPrev('');
            setFile(null);
		} catch (err) {
			setError(err.response.data.message);
		}
	}


    const onFileChange = (e) => {
        e.preventDefault();
        const [file] = e.target.files;

        const reader = new FileReader();

        reader.onload = () => {
            if(reader.readyState === 2){
                setImgPrev(reader.result);
                setFile(file);
            }
        }

        reader.readAsDataURL(file);
    }

    return (
        <section className='section profile-section'>
            <div className='container center'>
                <h2 className='common-heading'>My Profile</h2>
                <form className='form-container' onSubmit={handleSubmit}>
                    <input type='file' id='file' hidden={true} onChange={onFileChange} required/>
                    {
                        !imgPrev 
                        ? <label htmlFor='file'>
                            <div className='image-view'>
                                <CameraAltOutlined fontSize='large'/>
                            </div>
                        </label>
                        : <label htmlFor='file'>
                            <div className='img-prev'>
                                <img src={imgPrev} alt='our image'/>
                            </div>
                        </label>
                    }
                    <div className='textarea'>
                        <textarea placeholder='Enter Content Here...' onChange={(e) => setContent(e.target.value)} value={content}/>
                    </div>
                    <div className='button-field'>
                        <Button type='submit' variant='contained' sx={{ fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold' }}>Post Now</Button>
                    </div>
                </form>
            </div>
        </section>
    )
}

export default AddPost