import {Button} from '@mui/material';
import {Link, useNavigate} from 'react-router-dom';
import { useState, useContext } from 'react';
import objectToFormData from '../../utils/objectToFormData';
import { login } from '../../http/user';
import { userContext } from '../../providers/userProvider';

import './Auth.css';
import { notificationContext } from '../../providers/notificationProvider';

export default function Login(){
	const [data,setData] = useState({
		id: '',
		password: ''
	});

	const onChange = (e) => {
		setData({...data,[e.target.name]: e.target.value});
	}

	const {setUser} = useContext(userContext);
	const {setMessage,setError} = useContext(notificationContext);

	const navigate = useNavigate();

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			const FormData = objectToFormData(data);
			const {data:resData} = await login(FormData);
			setUser(resData.user);
			setMessage(resData.message);
			navigate('/');
		} catch (err) {
			setError(err.response.data.message);
		}
	}

	return(
			<>
				<section className='section login-section'>
					<div className='container center'>
						<h2 className='common-heading'>Welcome to <span>IT CLUB</span></h2>
						<form className='form-container' onSubmit={handleSubmit}>
							<div className='input-field'>
	                            <label htmlFor='email'>Email</label>
	                            <input type='text' placeholder='Enter Your Email Or Username' id='email' value={data.id} onChange={onChange} name='id' required/>
	                        </div>
	                        <div className='input-field'>
	                            <label htmlFor='password'>Password</label>
	                            <input type='password' placeholder='Enter Your Password' id='password' value={data.password} onChange={onChange} name='password' required/>
	                        </div>
	                        <div className='options'>
	                        	<p>Have not Account ? <Link to='/register'>Sing Up</Link></p>
	                        	<Link to='/forgotpassword'>Forgot Password ?</Link>
	                        </div>
	                        <div className='button-field'>
		                        <Button  type='submit' variant='contained' sx={{fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold'}}>Submit</Button>
		                    </div>
						</form>
					</div>
				</section>
			</>
		)
}