import { NavLink, Link,useNavigate } from 'react-router-dom';
import { useState,useContext } from 'react';
import { userContext } from '../../../providers/userProvider';
import { notificationContext } from '../../../providers/notificationProvider';
// icons 
import {Close, Menu} from '@mui/icons-material';

// style sheet 
import './Header.css';
import { logout } from '../../../http/user';

const Header = () => {

    const [open, setOpen] = useState(false);
    const {user, setUser} = useContext(userContext);
    const {setMessage,setError} = useContext(notificationContext);

    const navigate = useNavigate();
    const logothandler = async (e) => {
        e.preventDefault();
        try{
            const {data} = await logout();
            setUser(null);
            setMessage(data.message);
            navigate('/login');
        }catch(err){
            setError(err.response.data.message);
        }
    }
  return (
        <header className={`header ${open ? 'active' : ''}`}>
        {/* logo  */}
        <Link to='/'>
            <h2 className='logo'>IT CLUB</h2>
        </Link>

            {/* nav items */}
        <nav className='nav-items'>
            <ul>
                <li className='nav-item'><NavLink to='/'>Home</NavLink></li>
                <li className='nav-item'><NavLink to='/about'>About</NavLink></li>
                <li className='nav-item'><NavLink to='/services'>Service</NavLink></li>
                <li className='nav-item'><NavLink to='/contact'>Contact</NavLink></li>
                {user && <li className='nav-item'><NavLink to='/profile'>Profile</NavLink></li>}
                {user && <li className='nav-item'><NavLink to='/add-post'>Add Post</NavLink></li>}
                <li className='nav-item'><NavLink to='/posts/1'>Posts</NavLink></li>
                { user && <li className='nav-item'><a onClick={logothandler}>Logout</a></li>}
                
            </ul>
            {!user && <Link to='/login'>Login</Link>}
        </nav>

        {/* menu  */}
        <div className='menu' onClick={() => setOpen(!open)}>
            {
                !open ?
                <Menu sx={{fontSize: '5.5rem'}}/>:
                <Close sx={{fontSize: '5.5rem'}}/>
            }
        </div>
    </header>
  )
}

export default Header