import React from 'react'
import { Button } from '@mui/material';

import './Contact.css';

const Contact = () => {
  return (
    <>
        <div className='map'>
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d435519.2274097204!2d74.0047248169508!3d31.48310366143028!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39190483e58107d9%3A0xc23abe6ccc7e2462!2sLahore%2C%20Punjab%2C%20Pakistan!5e0!3m2!1sen!2sin!4v1672891429398!5m2!1sen!2sin" width="100%" height="300" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
        </div>
        <section className='section contact-section'>
            <div className='container center'>
                <h2 className='common-heading'>Contact Us</h2>
                <form className='contact-form'>
                    <div className='grid-two-col'>
                        <div className='input-field'>
                            <label htmlFor='name'>Name</label>
                            <input type='text' placeholder='Enter Your Name' id='name' required/>
                        </div>
                        <div className='input-field'>
                            <label htmlFor='email'>Email</label>
                            <input type='email' placeholder='Enter Your email' id='email' required/>
                        </div>
                    </div>
                    <div className='input-field'>
                        <label htmlFor='subject'>Subject</label>
                        <input type='text' placeholder='Enter Your subject' id='subject' required/>
                    </div>
                    <div className='input-field'>
                        <label htmlFor='message'>Message</label>
                        <textarea placeholder='Enter Your Message' id='message' required/>
                    </div>
                    <div className='button-field'>
                        <Button  type='submit' variant='contained' sx={{fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold'}}>Submit</Button>
                    </div>
                </form>
            </div>
        </section>
    </>
  )
}

export default Contact