import React, { useContext, useState } from 'react';
import {Link, useNavigate} from 'react-router-dom';
import {Button} from '@mui/material';

import '../auth/Auth.css';
import './Profile.css';
import { notificationContext } from '../../providers/notificationProvider';
import objectToFormData from '../../utils/objectToFormData';
import { changepassowrd } from '../../http/user';

const ChangePassword = () => {

    const [data, setData] = useState({
        oldpassword: '',
        newpassword: '',
        cnewpassword: ''

    });

    const onChange = (e) => {
		setData({...data,[e.target.name]: e.target.value});
	}

	const {setMessage,setError} = useContext(notificationContext);
    const navigate = useNavigate();
	const handleSubmit = async (e) => {
		e.preventDefault();
        if(data.cnewpassword !== data.newpassword){
            return setError('Confirm password does not match');
        }
		try {
			const FormData = objectToFormData(data);
			const {data: resData} = await changepassowrd(FormData);
			setMessage(resData.message);
            navigate('/profile');
		} catch (err) {
			setError(err.response.data.message);
		}
	}

    return (
        <section className='section profile-section'>
            <div className='container center'>
                <h2 className='common-heading'>Change Password</h2>
                <form className='form-container' onSubmit={handleSubmit}>
                    <div className='input-field'>
                        <label htmlFor='opassword'>Old Password</label>
                        <input type='text' placeholder='Enter Your Old Password' id='opassword' name='oldpassword' value={data.oldpassword} onChange={onChange} required />
                    </div>
                    <div className='input-field'>
                        <label htmlFor='npassword'>New Password</label>
                        <input type='text' placeholder='Enter Your New Password' id='npassword' name='newpassword' value={data.newpassword} onChange={onChange} required />
                    </div>
                    <div className='input-field'>
                        <label htmlFor='cnewpassword'>Confirm New Password</label>
                        <input type='text' placeholder='Enter Your Confirm New Password' id='cnewpassword' name='cnewpassword' value={data.cnewpassword} onChange={onChange} required />
                    </div>
                    <div className='button-field'>
                        <Button type='submit' variant='contained' sx={{ fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold' }}>change</Button>
                    </div>
                </form>
            </div>
        </section>
    )
}

export default ChangePassword;