import {Link} from 'react-router-dom';
import {Button} from '@mui/material';
import Services from '../services/Services';
import Contact from '../contact/Contact';

// style sheet
import './Home.css';

const Home = () => {
  return (
    <> 
      <section className='hero-section section'>
        <div className='container center'>
          <h1 className='heading'>Information Technology Club</h1>
          <p className='para'>
            Our team is always looking for skilled professionals to join us on a project-by-project basic. if you are self-motivated and looking for exiting opporunities to grow your skills and experience, we encourage you to get in touch to learn more about current idea's t get fulfilled in real Reach Us Now Common Let's have a Great Deal Together. Thanks You!
          </p>
          <Link to={'/services'}>
            <Button variant='contained' sx={{fontSize: '1.5rem', padding: '1rem 2rem', marginTop: '1.5rem', fontWeight: 'bold'}}>View Services</Button>
          </Link>
        </div>
      </section>
      <Services/>
      <Contact/>
    </>
  )
}

export default Home