import {Button} from '@mui/material';
import {Link} from 'react-router-dom';
import {notificationContext} from '../../providers/notificationProvider';
import {useContext, useState} from 'react';

import './Auth.css';
import { forgotpass } from '../../http/user';

export default function ForgotPassword(){
	const [email, setEmail] = useState('');
	const {setMessage,setError} = useContext(notificationContext);

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			const formData = new FormData();
			formData.append('email',email);
			// console.info(email);
			const {data} = await forgotpass(formData);
			setMessage(data.message);
		} catch (err) {
			setError(err.response.data.message);
		}
	}

	return(
			<>
				<section className='section login-section'>
					<div className='container center'>
						<h2 className='common-heading'>Forgot <span>Password</span></h2>
						<form className='form-container' onSubmit={handleSubmit}>
							<div className='input-field'>
	                            <label htmlFor='email'>Email</label>
	                            <input type='email' placeholder='Enter Your Email Or Username' id='email' required name='email' value={email} onChange={(e) => setEmail(e.target.value)}/>
	                        </div>
	                        <div className='button-field'>
		                        <Button  type='submit' variant='contained' sx={{fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold'}}>Forgot</Button>
		                    </div>
						</form>
					</div>
				</section>
			</>
		)
}