import { lazy, Suspense, useContext, useEffect } from "react";


import {BrowserRouter, Routes, Route} from 'react-router-dom';
import { userContext } from "./providers/userProvider";

// const Header = lazy(() => import('./components/layouts/header/Header'));
import Header from "./components/layouts/header/Header";

// const Footer = lazy(() => import('./components/layouts/footer/Footer'))
import Footer from './components/layouts/footer/Footer';

// const Home = lazy(() => import('./pages/home/Home'));
import Home from './pages/home/Home';

// const Services = lazy(() => import('./pages/services/Services'));
import Services from './pages/services/Services';

// const Contact = lazy(() => import('./pages/contact/Contact'));
import Contact from './pages/contact/Contact';

// const Login = lazy(() => import('./pages/auth/Login'));
import Login from './pages/auth/Login';

// const Register = lazy(() => import('./pages/auth/Register'));
import Register from './pages/auth/Register';

import ForgotPassword from './pages/auth/ForgotPassword';
import ResetPassword from './pages/auth/ResetPassword';
import Profile from './pages/profile/Profile';
import ChangePassword from './pages/profile/ChangePassword';
import AddPost from './pages/addpost/AddPost';
import 'react-toastify/dist/ReactToastify.css';

import AboutUs from './pages/about/About';
import Posts from './pages/posts/Posts';
import {notificationContext} from './providers/notificationProvider';
import {toast, ToastContainer} from 'react-toastify';

//style sheets
import('./App.css');

function App() {
  const {user} = useContext(userContext);
  const {message,setMessage,error, setError} = useContext(notificationContext);

  useEffect(() => {
    if(message){
      toast.info(message);
      setMessage(null);
    }

    if(error){
      toast.error(error);
      setError(null);
    }
  },[message,error]);
  return (
    <>
      <BrowserRouter>
        {/* <Suspense fallback={<div>Loading...</div>}> */}
          <Header/>
          <Routes>
            <Route path="/" element={<Home/>}/>
            <Route path='/services' element={<Services/>}/>
            <Route path='/contact' element={<Contact/>}/>

            <Route path="/about" element={<AboutUs/>}/>
            <Route path="/posts/:page" element={<Posts/>}/>

            {/* auth routes  */}
            {
              user &&
              <>
                <Route path='/profile' element={<Profile/>}/>
                <Route path='/change-password' element={<ChangePassword/>}/>
                <Route path='/add-post' element={<AddPost/>}/>
              </>
            }

            {/* no auth routes  */}
            {
              !user &&
              (<><Route path='/login' element={<Login/>}/>
              <Route path='/register' element={<Register/>}/>
              <Route path='/forgotpassword' element={<ForgotPassword/>} />
              <Route path='/resetpassword/:token' element={<ResetPassword/>}/></>)
            }
          </Routes>
          <Footer/>
          <ToastContainer
            position="bottom-center"
            autoClose={5000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            theme="dark"
          />
        {/* </Suspense> */}
      </BrowserRouter>
    </>
  );
}

export default App;
