import {Button} from '@mui/material';
import {Link, useNavigate} from 'react-router-dom';
import { useParams } from 'react-router-dom';
import {notificationContext} from '../../providers/notificationProvider';
import './Auth.css';
import { useState, useContext } from 'react';
import { resetpass } from '../../http/user';

export default function ForgotPassword(){
	const {token} = useParams();
	const [newPass, setNewPass] = useState();
	const [cNewPass, setCNewPass] = useState();
	const {setMessage,setError} = useContext(notificationContext);
	const navigate  = useNavigate();
	const handleSubmit = async (e) => {
		e.preventDefault();
		console.log(cNewPass,newPass)
		if(cNewPass !== newPass){
			return setError('Confirm Password does not match.');
		}
		try {
			const formData = new FormData();
			formData.append('password',newPass);
			const {data} = await resetpass(formData,token);
			setMessage(data.message);
			navigate('/login');
		} catch (err) {
			setError(err.response.data.message);
		}
	}
	return(
			<>
				<section className='section login-section'>
					<div className='container center'>
						<h2 className='common-heading'>Forgot <span>Password</span></h2>
						<form className='form-container' onSubmit={handleSubmit}>
							<div className='input-field'>
	                            <label htmlFor='password'>Password</label>
	                            <input type='text' placeholder='Enter Your Password' id='password' required name='password' value={newPass} onChange={(e) => setNewPass(e.target.value)}/>
	                        </div>
							<div className='input-field'>
	                            <label htmlFor='cpassword'>Confirm Password</label>
	                            <input type='text' placeholder='Enter Your Confirm Password' id='cpassword' required name='cpassword' value={cNewPass} onChange={(e) => setCNewPass(e.target.value)}/>
	                        </div>
	                        <div className='button-field'>
		                        <Button  type='submit' variant='contained' sx={{fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold'}}>Reset</Button>
		                    </div>
						</form>
					</div>
				</section>
			</>
		)
}