import React from 'react'
import './Service.css';
import ServiceCard from '../../components/servicecard/ServiceCard';

const Services = () => {
  const servicesData = [
    {
        title: 'Web Design',
        description: 'We are providing best web Designs services like E-commerce design, Portfolio design and Blog etc. If you need web Design service please contact us. Thanks You!'
    },
    {
        title: 'Web Development',
        description: 'We are providing the best web development services in cheapest prices like Mern Stack application, Block Chain development etc. Thanks You!'
    },
    {
        title: 'Photogrophy',
        description: 'We are providing best Photogrophy services. If you need any Photogrophy reletive services please contact us. Thanks You!'
    },
    {
        title: 'Android Development',
        description: 'We are providing best Mobile Apps services Android and IOS both. If you need any Mobile Apps services please contact us. Thanks You!'
    },
    {
        title: 'User Interface',
        description: 'We are providing best UI (User Interface). If you need any UI (User Interface) services please contact us. Thanks You!'
    },
    {
        title: 'Graphic Design',
        description: 'We are providing best Graphic Designing service like Photo Editing, Video Editing etc. If you need any Graphic Designing services please contact us. Thanks You!'
    },
  ]
  return (
    <section className='service-section section'>
        <div className='container center'>
            <h2 className='common-heading'>Our Services</h2>
            <div className='service-container'>
                {
                    servicesData.map(({title, description}) => (<ServiceCard title={title} description={description}/>))
                }
            </div>
        </div>
    </section>
  )
}

export default Services