import axios from 'axios';

const REACT_PUBLIC_BACKEND_URL = '/';
const api = axios.create({
    baseURL: `${REACT_PUBLIC_BACKEND_URL}/api/v1`,
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json'
    }
});


export const register = async (FormData) => await api.post('/register',FormData);
export const login = async (FormData) => await api.post('/login',FormData);
export const loadme = async () => await api.get('/me');
export const logout = async () => await api.get('/logout');
export const updateUser = async (FormData) => await api.put('/user/update',FormData);
export const changepassowrd = async (FormData) => await api.put('/user/change-password',FormData);
export const fetchPost = async (page) => await api.get(`/post/get/${page}`);
export const deletePost = async (id) => await api.delete(`/post/delete/${id}`);
export const forgotpass = async (FormData) => await api.post(`/forgot-password`,FormData);
export const resetpass = async (FormData,token) => await api.put(`/reset-password/${token}`,FormData);
export const addPost = async (FormData) => await api.post('/post/add',FormData,{
    headers: {
        'Content-Type':'multipart/form-data'
    }
});