import React, { useContext, useState } from 'react';
import {Link} from 'react-router-dom';
import {Button} from '@mui/material';

import '../auth/Auth.css';
import './Profile.css';
import { userContext } from '../../providers/userProvider';
import { notificationContext } from '../../providers/notificationProvider';
import objectToFormData from '../../utils/objectToFormData';
import { updateUser } from '../../http/user';

const Profile = () => {
    const {user} = useContext(userContext);

    const [data, setData] = useState({
        name: user?.name,
        email: user?.email,
        username: user?.username
    });

    const onChange = (e) => {
		setData({...data,[e.target.name]: e.target.value});
	}

	const {setMessage,setError} = useContext(notificationContext);

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			const FormData = objectToFormData(data);
			const {data: resData} = await updateUser(FormData);
			setMessage(resData.message)
		} catch (err) {
			setError(err.response.data.message);
		}
	}

    return (
        <section className='section profile-section'>
            <div className='container center'>
                <h2 className='common-heading'>My Profile</h2>
                <form className='form-container' onSubmit={handleSubmit}>
                    <div className='input-field'>
                        <label htmlFor='name'>Name</label>
                        <input type='text' placeholder='Enter Your Name' id='name' name='name' value={data.name} onChange={onChange} required />
                    </div>
                    <div className='input-field'>
                        <label htmlFor='email'>Email</label>
                        <input type='email' placeholder='Enter Your Email' id='email' name='email' value={data.email} onChange={onChange} required />
                    </div>
                    <div className='input-field'>
                        <label htmlFor='username'>Username</label>
                        <input type='text' placeholder='Enter Your Username' id='username' name='username' value={data.username} onChange={onChange} required />
                    </div>
                    <div className='options'>
	                    <p>If you change password ? <Link to='/change-password'>click here</Link></p>
	                </div>
                    <div className='button-field'>
                        <Button type='submit' variant='contained' sx={{ fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold' }}>Update</Button>
                    </div>
                </form>
            </div>
        </section>
    )
}

export default Profile