import React,{useContext} from 'react'
import './PostsCart.css'
import {Delete} from '@mui/icons-material';
import {userContext} from '../../providers/userProvider';
import {notificationContext} from '../../providers/notificationProvider';
import { deletePost } from '../../http/user';


function DateFormat (createdAt){
  const date = new Date(createdAt);
  const day = date.getDate();
  const month = date.getMonth();
  const year = date.getFullYear();

  return `${day}/${month}/${year}`
}

const PostsCart = ({image:{url},content,email,_id,author,createdAt}) => {
  const {user} = useContext(userContext);
  const {setMessage, setError} = useContext(notificationContext);
  const deleteHandle = async () => {
    try {
      const {data} = await deletePost(_id);
      setMessage(data.message);
    } catch (error) {
        setError(error.response.data.message);
    }
  }
  return (
    <div className='cart'>
        <div className='topbar'>
          <div className='user'>
            <img src='/images/hero-bg.jpg'/>
            <div className='details'>
              <h5>{author.name[0].toUpperCase() + author.name.slice(1)}</h5>
              <time>{DateFormat(createdAt)}</time>
            </div>
          </div>
          {user && user.email === author.email && <button className='delete' onClick={deleteHandle}><Delete fontSize='large'/></button>}
        </div>
        <div className='posts-content'>
          <div className='image'>
            <img src={url}/>
          </div>
          <div className='contant'>
            <p>{content}</p>
          </div>
        </div>
    </div>
  )
}

export default PostsCart