import React,{useEffect,useContext} from 'react';
import './Posts.css';
import PostsCart from '../../components/postscart/PostsCart';
import { fetchPost } from '../../http/user';
import { postContext } from '../../providers/postProvider';
import {useParams} from 'react-router-dom';
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';
import { useNavigate,useLocation } from 'react-router-dom';


const Posts = () => {
  const {page} = useParams();
  const {setPost, post, totalPage,setTotalPage} = useContext(postContext);
  const nagivate = useNavigate();
  const {pathname} = useLocation();
  const getPost = async () => {
    try {
       const {data} = await fetchPost(page);
       setPost(data.posts); 
       setTotalPage(data.totalPage)
    } catch (err) {
        console.log(err.message);
    }
  }
  useEffect(() => {
      getPost();
  },[pathname]);

  const handleChange = (event, value) => {
    nagivate(`/posts/${value}`);
  };
  return (
    <section className='section posts-section'>
        <div className='container'>
            <div className='posts-container'>
                {
                  post && post.map((data) => <PostsCart {...data}/>)
                }
            </div>
            <Stack spacing={2} alignItems={'center'}>
              <Pagination count={totalPage}  color="primary" page={+page} size="large" onChange={handleChange}/>
            </Stack>
        </div>
    </section>
  )
}

export default Posts