import {Button} from '@mui/material';
import {Link, useNavigate} from 'react-router-dom';

import './Auth.css';
import { useState, useContext } from 'react';
import { register } from '../../http/user';
import objectToFormData from '../../utils/objectToFormData';
import {userContext} from '../../providers/userProvider';
import { notificationContext } from '../../providers/notificationProvider';

export default function Register(){
	const [data, setData] = useState({
		name: '',
		email: '',
		username: '',
		password: ''
	});

	const {setUser} = useContext(userContext);
	const {setMessage,setError} = useContext(notificationContext);
	const navigate = useNavigate();

	const onChange = (e) => {
		setData({...data,[e.target.name]: e.target.value});
	}

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			const FormData = objectToFormData(data);
			const {data: resData} = await register(FormData);
			setUser(resData.user);
			setMessage(resData.message)
			navigate('/');
		} catch (err) {
			setError(err.response.data.message);
		}
	}
	return(
			<>
				<section className='section login-section'>
					<div className='container center'>
						<h2 className='common-heading'>Welcome to <span>IT CLUB</span></h2>
						<form className='form-container' onSubmit={handleSubmit}>
							<div className='input-field'>
	                            <label htmlFor='name'>Name</label>
	                            <input type='text' placeholder='Enter Your Name' id='name' name='name' onChange={onChange} required/>
	                        </div>
							<div className='input-field'>
	                            <label htmlFor='email'>Email</label>
	                            <input type='email' placeholder='Enter Your Email' id='email' name='email' onChange={onChange} required/>
	                        </div>
	                        <div className='input-field'>
	                            <label htmlFor='username'>Username</label>
	                            <input type='text' placeholder='Enter Your Username' id='username' name='username' onChange={onChange} required/>
	                        </div>
	                        <div className='input-field'>
	                            <label htmlFor='password'>Password</label>
	                            <input type='password' placeholder='Enter Your Password' id='password' name='password' onChange={onChange} required/>
	                        </div>
	                        <div className='options'>
	                        	<p>Already have Account? <Link to='/login'>Login</Link></p>
	                        	<Link to='/forgotpassword'>Forgot Password ?</Link>
	                        </div>
	                        <div className='button-field'>
		                        <Button  type='submit' variant='contained' sx={{fontSize: '1.5rem', padding: '1rem 5rem', marginTop: '1.5rem', fontWeight: 'bold'}}>Register</Button>
		                    </div>
						</form>
					</div>
				</section>
			</>
		)
}